import React, { useEffect, useState } from 'react'
import Modal from "react-modal";
import { customStyles } from '../../helpers/customStylesModal';
import DateTimePicker from 'react-datetime-picker';
import moment from 'moment/moment';

import 'react-datetime-picker/dist/DateTimePicker.css';
import 'react-calendar/dist/Calendar.css';
import 'react-clock/dist/Clock.css';
import Swal from 'sweetalert2';
import { useDispatch, useSelector } from 'react-redux';
import { closeModalAction } from '../../actions/modal';
import { eventStartAddNew, eventCleanActiveEvent, eventStartUptaded } from '../../actions/events';


Modal.setAppElement('#root');
const now = moment().minutes(0).seconds(0).add(1, "hours");
const nowPlus1 = now.clone().add(1, "hour")

const initEvent = {
    title: "",
    notes: "",
    start: now.toDate(),
    end: nowPlus1.toDate()
}


const CalendarModal = () => {
    const dispatch = useDispatch();
    const { modalOpen } = useSelector(state => state.ui);
    const { activeEvent } = useSelector(state => state.calendar);

    const [dateStart, setDateStart] = useState(now.toDate());
    const [dateEnd, setDateEnd] = useState(nowPlus1.toDate());
    const [titleValid, setTitleValid] = useState(true);

    const [formValues, setFormValues] = useState(initEvent)
    const { notes, title, start, end } = formValues;
    useEffect(() => {
        if (activeEvent) {
            setFormValues(activeEvent)
        } else {
            setFormValues(initEvent)
        }
    }, [activeEvent, setFormValues])




    const handleInputChange = ({ target }) => {
        setFormValues(
            {
                ...formValues,
                [target.name]: target.value
            }
        )
    }

    const closeModal = () => {
        dispatch(eventCleanActiveEvent())
        dispatch(closeModalAction())
        setFormValues(initEvent)
    }



    const handleStartDateChange = (e) => {
        setDateStart(e);
        setFormValues(
            {
                ...formValues,
                start: e
            }

        )
    }
    const handleEndDateChange = (e) => {
        setDateEnd(e);
        setFormValues(
            {
                ...formValues,
                end: e
            }
        )
    }


    const handleSubmitForm = (e) => {
        e.preventDefault();
        const momentStart = moment(start);
        const momentEnd = moment(end);

        if (momentStart.isSameOrAfter(momentEnd)) {
            return Swal.fire("Error", "La fecha fin debe de ser mayor a la fecha de inicio")
        }
        if (title.trim().length < 2) {
            return setTitleValid(false);
        }
        if (activeEvent) {
            dispatch(eventStartUptaded(formValues))

        } else {

            dispatch(eventStartAddNew(formValues));
            setTitleValid(true);
        }
        closeModal()
    }

    return (
        <Modal
            isOpen={modalOpen}
            /* onAfterOpen={afterOpenModal} */
            onRequestClose={closeModal}
            style={customStyles}
            closeTimeoutMS={200}
            className="modal"
            overlayClassName="modal-fondo"
        >
            {
                (activeEvent) ? <h1>Editar Evento</h1> : <h1>Nueva Nota</h1>
            }
            <hr />
            <form className="container" onSubmit={handleSubmitForm}>

                <div className="form-group mb-2">
                    <label>Fecha y hora inicio</label>
                    <DateTimePicker className="form-control" onChange={handleStartDateChange} value={dateStart} />
                </div>

                <div className="form-group mb-2">
                    <label>Fecha y hora fin</label>
                    <DateTimePicker className="form-control" onChange={handleEndDateChange} value={dateEnd} minDate={dateStart} />
                </div>

                <hr />
                <div className="form-group mb-2">
                    <label>Titulo y notas</label>
                    <input
                        type="text"
                        className={`form-control ${!titleValid && "is-invalid"}`}
                        placeholder="Título del evento"
                        name="title"
                        value={title}
                        onChange={handleInputChange}
                        autoComplete="off"
                    />
                    <small id="emailHelp" className="form-text text-muted">Una descripción corta</small>
                </div>

                <div className="form-group mb-2">
                    <textarea
                        type="text"
                        className="form-control"
                        placeholder="Notas"
                        rows="5"
                        name="notes"
                        value={notes}
                        onChange={handleInputChange}
                    ></textarea>
                    <small id="emailHelp" className="form-text text-muted">Información adicional</small>
                </div>

                <button
                    type="submit"
                    className="btn btn-outline-primary btn-block"
                >
                    <i className="far fa-save"></i>
                    <span> Guardar</span>
                </button>

            </form>
        </Modal>
    )
}

export default CalendarModal
