import React, { useEffect, useState } from 'react';
import NavBar from '../ui/NavBar';
import CalendarModal from './CalendarModal';
import moment from 'moment/moment';
import "moment/locale/es";
import { Calendar, momentLocalizer } from 'react-big-calendar';
import "react-big-calendar/lib/css/react-big-calendar.css";
import { messages } from '../../helpers/calentar-messages-es';
import CalendarEvent from './CalendarEvent';
import { useDispatch, useSelector } from 'react-redux';
import { openModalAction } from '../../actions/modal';
import { eventCleanActiveEvent, eventStartLoading, eventsetActive } from '../../actions/events';
import AddNewFab from '../ui/AddNewFab';
import DeleteEventFab from '../ui/DeleteEventFab';

moment.locale("es");
const localizer = momentLocalizer(moment);

/* const events = [{
  title: "viernes de putas",
  start: moment().toDate(),
  end: moment().add(2, "hours").toDate(),
  bgcolor: "#682383",
  user: {
    _id: "123",
    name: "fernando"
  }
}] */

const CalendarScreen = () => {
  const dispatch = useDispatch();
  const { events, activeEvent } = useSelector(state => state.calendar);
  const { uid } = useSelector(state => state.auth);
  const [lastView, setlastView] = useState(localStorage.getItem("lastView") || "month")


  useEffect(() => {
    dispatch(eventStartLoading());
  
  }, [dispatch])
  

  const onDoubleClick = (e) => {
    dispatch(openModalAction())
  }
  const onSelectEvent = (e) => {
    dispatch(eventsetActive(e))
  }


  const onViewChange = (e) => {
    setlastView(e)
    localStorage.setItem("lastView", e);

  }
  const onSelectSlot=(e)=>{
    dispatch(eventCleanActiveEvent())
  }

  const eventStyleGetter = (event, start, end, isSelected) => {

    const style = {
      backgroundColor: (uid === event.user._id)?  "#367CF7":"#463660",
      borderRadius: "4px",
      display: "block",
      color: "white"
    }
    return {
      style
    }
  }

  return (
    <div className='calendar-screen'>
      <NavBar></NavBar>

      <Calendar
        localizer={localizer}
        events={events}
        startAccessor="start"
        endAccessor="end"
        messages={messages}
        eventPropGetter={eventStyleGetter}
        onDoubleClickEvent={onDoubleClick}
        onSelectSlot={onSelectSlot}
        selectable={true}
        onSelectEvent={onSelectEvent}
        onView={onViewChange}
        view={lastView}
        components={{
          event: CalendarEvent
        }}
      />
      {
        (activeEvent) && <DeleteEventFab></DeleteEventFab>
      }
      <AddNewFab></AddNewFab>
      <CalendarModal></CalendarModal>
    </div>
  )
}

export default CalendarScreen
